import { Component, Host, Prop, State, Watch, Element, Event, EventEmitter, h } from '@stencil/core';
import { PropsService } from '../../services/props.service';
import { ReportService } from '../../services/report.service';
// import { TranslateService } from '../../services/translate.service';
import { StateService } from '../../services/state.service';
import { TranslateService } from '../../services/translate.service';

@Component({
  tag: 'template-chooser',
  styleUrl: 'template-chooser.scss',
  shadow: true,
})
export class TemplateChooser {
  @Prop() langObj: any;  
  @Prop() selectedTemplateId: any;  
  @Watch('selectedTemplateId')
  selectedTemplateIdChanged() {
    this.reportService.setParamCache({
      templateItemId: this.selectedTemplateId
    });
  }

  @Prop() templateIds: string;
  @Watch('templateIds')
  templateIdsChanged() {
    this.init();
  }
  
  @Element() element: HTMLElement;
  @Event() selectedTemplateChange: EventEmitter<any>;
  @State() templates: any[] =[];
  @State() translator: any;
  private reportService: ReportService = ReportService.getService();
  private stateService: StateService = StateService.getService();

  componentWillLoad() {
    this.reportService.setParamCache({
      templateItemId: this.selectedTemplateId
    });
    return Promise.resolve(true)
    // .then(() => {
    //   return TranslateService.getService().getTranslate();
    // })
    .then(() => {
      const res = TranslateService.getService().getTranslateSync();
      this.translator = res;
      this.stateService.subscribe('locale-data-changed', (data) => {
        this.translator = data;
      });
      return this.init();
    });
  }

  init() {
    const globalProps = PropsService;
    const reportService: ReportService = ReportService.getService();
    return Promise.resolve(true).then(() => {
      if (this.templateIds === undefined) {
        return reportService.getReportTemplates(globalProps.surveyItemId);
      } else {
        return reportService.getReportTemplates(globalProps.surveyItemId, {templateIds: this.templateIds});
      }
    })
    .then((templates) => {
      this.templates = templates;
      this.stateService.notifyDataChanged('print-templates-updated', {value: templates});
      this.reportService.setHelperObj({
        printTemplates: templates
      });
      // use the first one as default, if the selected template id is not provided
      if (!this.selectedTemplateId && templates.length) {
        this.selectedTemplateId = templates[0].id;
        this.reportService.setParamCache({
          templateItemId: this.selectedTemplateId
        });
        this.selectedTemplateChange.emit(this.selectedTemplateId);
      }
      return true;
    }).catch((err) => {
      this.reportService.showError(err);
    })
  }

  /**
   * 
   * @param evt 
   */
  public selectedTemplateChangeHandler(evt) {
    this.reportService.setParamCache({
      templateItemId: evt.target.value
    });

    this.selectedTemplateChange.emit(evt.target.value);
  }

  render() {
    return (
      <Host>
        {/* <calcite-label> */}
          <div class="heading">{this.translator?.customPrint.chooseTemplateLabel1}</div>
          <calcite-combobox placeholder={this.translator?.customPrint.chooseTemplateLabel1} selection-mode="single-persist" clearDisabled="true" value={this.selectedTemplateId} onCalciteComboboxChange={(evt) => {this.selectedTemplateChangeHandler(evt)}}>
            {(this.templates || []).map((template) => {
              return <calcite-combobox-item value={template.id} text-label={template.title} selected={template.id===this.selectedTemplateId}></calcite-combobox-item>;
            })}
          </calcite-combobox>
          {(this.templates || []).length > 0 ?
            null : 
            <calcite-notice open kind="danger" scale="s" width="auto"> 
                <div slot="message">{this.translator?.customPrint.chooseTemplateNoOneYet1}</div>
            </calcite-notice>
          }
        {/* </calcite-label> */}
        <slot></slot>
      </Host>
    );
  }

}
